import { AdminLayout } from '@/components/AdminLayout'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Users, Shield, Key, Activity, TrendingUp, Clock, CheckCircle2, AlertCircle } from 'lucide-react'
import { useEffect, useState } from 'react'
import { fetchWithAuth, authService } from '@/lib/auth'
import { useNavigate } from 'react-router-dom'

interface DashboardStats {
  totalUsers: number
  totalRoles: number
  totalApiKeys: number
  activeKeys: number
}

export default function DashboardPage() {
  const navigate = useNavigate()
  const [stats, setStats] = useState<DashboardStats>({
    totalUsers: 0,
    totalRoles: 0,
    totalApiKeys: 0,
    activeKeys: 0
  })
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Check if user is root (only root can access dashboard)
    const user = authService.getUser()
    const isRoot = user?.role === 'root'

    if (!isRoot) {
      // Redirect non-root users to profile
      navigate('/profile')
      return
    }

    fetchStats()
  }, [navigate])

  const fetchStats = async () => {
    try {
      // Fetch users
      const usersRes = await fetchWithAuth('/api/admin/users')
      if (usersRes.ok) {
        const users = await usersRes.json()
        setStats(prev => ({ ...prev, totalUsers: users.length }))
      }

      // Fetch roles
      const rolesRes = await fetchWithAuth('/api/roles')
      if (rolesRes.ok) {
        const rolesData = await rolesRes.json()
        setStats(prev => ({ ...prev, totalRoles: rolesData.roles?.length || 0 }))
      }
    } catch (error) {
      console.error('Error fetching stats:', error)
    } finally {
      setLoading(false)
    }
  }

  const statCards = [
    {
      title: 'Total de Usuarios',
      value: stats.totalUsers,
      description: '+12% respecto al mes pasado',
      icon: Users,
      bgColor: 'bg-gradient-to-br from-blue-50 to-blue-100',
      iconColor: 'bg-blue-500',
      textColor: 'text-blue-700'
    },
    {
      title: 'Roles Activos',
      value: stats.totalRoles,
      description: 'Roles del sistema configurados',
      icon: Shield,
      bgColor: 'bg-gradient-to-br from-emerald-50 to-emerald-100',
      iconColor: 'bg-emerald-500',
      textColor: 'text-emerald-700'
    },
    {
      title: 'Claves API',
      value: stats.totalApiKeys,
      description: 'Total de claves generadas',
      icon: Key,
      bgColor: 'bg-gradient-to-br from-purple-50 to-purple-100',
      iconColor: 'bg-purple-500',
      textColor: 'text-purple-700'
    },
    {
      title: 'Sesiones Activas',
      value: stats.activeKeys,
      description: 'Actualmente en línea',
      icon: Activity,
      bgColor: 'bg-gradient-to-br from-amber-50 to-amber-100',
      iconColor: 'bg-amber-500',
      textColor: 'text-amber-700'
    }
  ]

  return (
    <AdminLayout>
      <div className="space-y-6 max-w-7xl">
        {/* Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              Panel de Control
            </h1>
            <p className="mt-2 text-gray-600 flex items-center gap-2">
              <Clock className="h-4 w-4" />
              Bienvenido de nuevo, Admin
            </p>
          </div>
          <div className="flex items-center gap-2 px-4 py-2 bg-green-50 rounded-xl border border-green-200">
            <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
            <span className="text-sm font-medium text-green-700">Todos los Sistemas Operativos</span>
          </div>
        </div>

        {/* Stats Cards */}
        {loading ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 lg:gap-6">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="animate-pulse">
                <div className="h-32 bg-gray-100 rounded-2xl"></div>
              </div>
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 lg:gap-6">
            {statCards.map((stat, index) => (
              <div
                key={stat.title}
                className="group relative overflow-hidden rounded-2xl shadow-sm hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1"
                style={{ animationDelay: `${index * 100}ms` }}
              >
                <div className={`p-6 ${stat.bgColor}`}>
                  <div className="flex items-start justify-between mb-4">
                    <div className={`p-3 rounded-xl ${stat.iconColor} shadow-lg`}>
                      <stat.icon className="h-6 w-6 text-white" />
                    </div>
                    <TrendingUp className="h-5 w-5 text-green-500" />
                  </div>
                  <div>
                    <p className="text-sm font-medium text-gray-600 mb-1">{stat.title}</p>
                    <p className={`text-3xl font-bold ${stat.textColor} mb-2`}>{stat.value}</p>
                    <p className="text-xs text-gray-500">{stat.description}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {/* System Status Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Quick Stats */}
          <Card className="rounded-2xl shadow-sm border-0 bg-gradient-to-br from-white to-gray-50">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <CheckCircle2 className="h-5 w-5 text-green-500" />
                Estado del Sistema
              </CardTitle>
              <CardDescription>Monitoreo en tiempo real del sistema</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="flex items-center justify-between p-4 bg-white rounded-xl border border-gray-100">
                  <div className="flex items-center gap-3">
                    <div className="p-2 bg-green-100 rounded-lg">
                      <Activity className="h-5 w-5 text-green-600" />
                    </div>
                    <div>
                      <p className="font-semibold text-gray-900">Servidor API</p>
                      <p className="text-sm text-gray-500">Tiempo de respuesta: 45ms</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
                    <span className="text-sm font-medium text-green-600">Saludable</span>
                  </div>
                </div>

                <div className="flex items-center justify-between p-4 bg-white rounded-xl border border-gray-100">
                  <div className="flex items-center gap-3">
                    <div className="p-2 bg-blue-100 rounded-lg">
                      <Shield className="h-5 w-5 text-blue-600" />
                    </div>
                    <div>
                      <p className="font-semibold text-gray-900">Base de Datos</p>
                      <p className="text-sm text-gray-500">MySQL 8.0 conectada</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
                    <span className="text-sm font-medium text-green-600">Activa</span>
                  </div>
                </div>

                <div className="flex items-center justify-between p-4 bg-white rounded-xl border border-gray-100">
                  <div className="flex items-center gap-3">
                    <div className="p-2 bg-purple-100 rounded-lg">
                      <Key className="h-5 w-5 text-purple-600" />
                    </div>
                    <div>
                      <p className="font-semibold text-gray-900">Autenticación</p>
                      <p className="text-sm text-gray-500">Tokens JWT válidos</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
                    <span className="text-sm font-medium text-green-600">Seguro</span>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Recent Activity */}
          <Card className="rounded-2xl shadow-sm border-0 bg-gradient-to-br from-white to-gray-50">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <AlertCircle className="h-5 w-5 text-blue-500" />
                Actividad Reciente
              </CardTitle>
              <CardDescription>Últimos eventos del sistema</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {[
                  { action: 'Inicio de sesión de usuario', user: 'admin', time: 'hace 2 minutos', type: 'success' },
                  { action: 'Clave API creada', user: 'admin', time: 'hace 1 hora', type: 'info' },
                  { action: 'Rol actualizado', user: 'admin', time: 'hace 3 horas', type: 'warning' }
                ].map((activity, index) => (
                  <div key={index} className="flex items-center gap-4 p-4 bg-white rounded-xl border border-gray-100">
                    <div className={`p-2 rounded-lg ${
                      activity.type === 'success' ? 'bg-green-100' :
                      activity.type === 'info' ? 'bg-blue-100' : 'bg-amber-100'
                    }`}>
                      <div className={`h-2 w-2 rounded-full ${
                        activity.type === 'success' ? 'bg-green-500' :
                        activity.type === 'info' ? 'bg-blue-500' : 'bg-amber-500'
                      }`}></div>
                    </div>
                    <div className="flex-1">
                      <p className="font-medium text-gray-900">{activity.action}</p>
                      <p className="text-sm text-gray-500">por {activity.user}</p>
                    </div>
                    <span className="text-xs text-gray-400">{activity.time}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </AdminLayout>
  )
}
